@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Recursive&display=swap');
* {
  /* background-color: #EEEBDD; */
  /* color: rgb(10, 13, 56); */
  margin: 0; padding: 0;
  font-family: 'Recursive', sans-serif;
}
.header {
  font-weight: 400;
  font-size: 1.9rem;
  margin: 25px auto 35px;
  text-align: center;
  letter-spacing: 2px;
  text-shadow: 0 0 2px rgb(142, 172, 255);
}
/* Search Line */

.country-selection {
  padding: 10px 17%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 70vw;
}
.country-title {
  color:rgba(10, 13, 56, 0.9);
  font-size: 1rem;
}
.country-newline {
  display: none;
}
.country-dropdown {
  flex-grow: 1;
  display: flex;
}
.country-search {
  background: rgba(110, 110, 250, 0.178);
  margin: auto 15px;
  border: none;
  border-radius: 4px;
  padding: 18px 20px 18px 16px;
  font-size: 1rem;
  flex: 1;
}
.country-search:after {
  padding: 18px;
  content: '>>';
}
.country-search:focus, .country-search:hover {
  outline: none;
  box-shadow: 0 0 8px 5px rgba(147, 147, 218, 0.144);
  transition-duration: 0.3s;
}

::-webkit-scrollbar {
  width: 6px; height: 6px;
  margin-left: 3px;
  background-color: #d7dad5;
}
::-webkit-scrollbar-thumb {
  background: rgba(10, 13, 56, 0.637);
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(10, 13, 56, 0.75);
}

.case-boxes {
  padding: 0 10px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.last-updated {
  display: flex;align-items: center;justify-content:center;font-size: 0.88rem;color: rgb(29, 30, 31);
  margin: 10px;
}

/* Container CSS -- to make two partition in the app, one on left, one on right. left has - boxes, dropdown --- right has - table */
.container {
  display: flex;
  padding: 2% 20px 0 0;
}
.left {
  width: 60vw;
}
.right {
  width: 40vw;
  padding: auto 20px;
}
/* Media Queries */
@media screen and (max-width: 670px) {
  .country-selection {
    padding: 10px 8%;
  }
  .country-title {
    flex-basis: 100%;
    font-size: 0.9rem;
    margin-bottom: 8px;
  }
  .country-search {
    margin-left: 0;
  }
  .container {
    flex-direction: column;
  }
  .left , .right {
    width: 100%;
  }
  .right {
    padding: 5px 15px;
  }
}

@media screen and (max-width: 500px) {
  .case-boxes {
    padding: 2% 3%;
  }
  .country-search {
    width: 100%;
  }
  .country-selection {
    padding: 2% 5%;
  }
}