.tableData {
    overflow: scroll;
    position: relative;
    height: 380px; margin-top: 10px;
    overflow-x: hidden;
    color: #3d3d3d!important;
    /* border: 1px solid rgb(170, 169, 169); */
}
.table-row {
    display: flex;
    padding: 8px;
    justify-content: space-between;
    animation-duration: 0.5s;
}
.table-row:hover , .table-row:focus {
    cursor: pointer;
    box-shadow: 0 0 3px 3px rgba(177, 173, 173, 0.15);
    background-color:rgba(240, 235, 235, 0.664);
}
.table-row:nth-child(odd) {
    background-color: #dddddd;
}
.diff-row {
    padding: 9px; position: sticky;
    background-color: #353434!important; color: #fff!important;
    font-weight: 400;
}
